.calendar {
  display: flex;
  gap: $gap;
  justify-content: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  width: calcWidth($square-size,$gap);

  &__block {
    display: block;
    text-align: center;
    line-height: $square-size;
    @include square($square-size);

    background-color: #eee;
    border: 1px solid black;
    transition: $transition-time, transform $transition-time;

    &:hover {
      transform: translate(0, -20px);
      background-color: pink;
      cursor: pointer;
    }
  }
}
